<script>
import * as d3 from 'd3';
import { select } from 'd3-selection';
import { hierarchy, tree } from 'd3-hierarchy';
// import Storage from './Storage.vue'
export default {
  name: "Attention",
  components: { 
    
  },
  mounted() {
    this.drawIciclePlot();
  },
  methods: {
    drawIciclePlot() {
      const width = 1750;
      const height = 450;

      const svg = d3
        .select('#icicle-plot-container')
        .append('svg')
        .attr('width', width)
        .attr('height', height);

      const color = d3.scaleOrdinal()
                    // .range(['#2b8cbe', '#4eb3d3', '#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
                    .range(["#4eb3d3","#7bccc4","#a8ddb5","#ccebc5","#e0f3db","#f7fcf0"])


      const partition = d3.partition().size([width, height]);
      
      const data = {
          name: '出现以下情况，注意停药并咨询医师',
          children: [
            {
              name: '症状未缓解',
              // value: 10,
              children: [
                { name: '用于解热连续使用不超过3天', value: 2 },
                { name: '用于止痛连续使用不超过5天', value: 2 },
                { name: '症状持续或恶化', value: 2 },
                { name: '出现新的症状', value: 2 }
              ]
            },
            {
              name: '用药后出现皮疹或过敏反应的其他征象',
              // value: 8,
              children: [
                { name: '瘙痒', value: 2 },
                { name: '皮疹', value: 2 },
                { name: '口腔、眼、外生殖器红斑、糜烂', value: 2}
              ]
            },
            {
              name: '过量服用',
              // value: 10,
              children: [
                { name: '无论是否出现不适症状', value: 2 },
                
              ]
            },
            {
              name: '服药期间发现肝生化指标异常或出现可能与肝损伤有关的临床表现',
              // value: 8,
              children: [
                { name: '全身乏力', value: 2 },
                { name: '食欲不振', value: 2 },
                { name: '厌油', value: 2},
                { name: '恶心', value: 2 },
                { name: '上腹胀痛', value: 2 },
                { name: '尿黄', value: 2},
                { name: '目黄', value: 2 },
                { name: '皮肤黄染', value: 2 },
              ]
            }
          ]
        };

      const root = d3.hierarchy(data)
        .sum((d) => d.value)
        .sort((a, b) => b.value - a.value);

      partition(root);

      const cell = svg
        .selectAll('g')
        .data(root.descendants())
        .join('g')
        .attr('transform', (d) => `translate(${d.x0},${d.y0})`)
        .attr('class', 'rectangle') //添加rectangle类

      cell
        .append('rect')
        .attr('width', (d) => d.x1 - d.x0)
        .attr('height', (d) => d.y1 - d.y0)
        .attr('fill', (d) => color((d.children ? d : d.parent).data.name))
      
        // 在foreignObject元素内部创建了一个div元素，并使用CSS样式将其设置为占据foreignObject的整个宽度和高度。
        // 通过设置display: flex、justify-content: center和align-items: center，将文本内容水平和垂直居中显示在单元格中。
        cell
        .append('foreignObject')
        .attr('x', 4) // 偏移量，根据需求调整
        .attr('y', 4) // 偏移量，根据需求调整
        .attr('width', (d) => d.x1 - d.x0 - 8) // 减去边距的宽度
        .attr('height', (d) => d.y1 - d.y0 - 8) // 减去边距的高度
        .append('xhtml:div')
        // .style('width', (d) => d.x1 - d.x0 - 8 + 'px') // 减去边距的宽度
        // .style('height', (d) => d.y1 - d.y0 - 8 + 'px') // 减去边距的高度
        // .style('overflow', 'auto') // 添加滚动条，根据需要调整
        .style('width', '100%')
        .style('height', '100%')
        .style('display', 'flex')
        .style('justify-content', 'center')
        .style('align-items', 'center')
        // .style('color', 'white')
        .style('font-weight',700)
        // .style('title',(d) => d.data.name)
        .html((d) => d.data.name)
        .on('mouseover', function () {
            d3.select(this)
                .style('background-color', 'rgba(255, 255, 120)'); // 在鼠标悬浮时改变背景颜色为黄色
        })
        .on('mouseout', function () {
            d3.select(this)
                .style('background-color', null); // 在鼠标移出时恢复原始背景颜色
        });

        // ...

    },
  }
};

</script>

