// 该文件专门用于创建整个应用的路由器
import Vue from 'vue'
import VueRouter from "vue-router";
//使用插件
Vue.use(VueRouter)
// 引入页面组件
import AppMain from '../pages/AppMain'
// 引入路由页面组件
import PatientMain from '../pages/PatientMain'
import ProfesMain from '../pages/ProfesMain'

// 创建并暴露一个路由器
export default new VueRouter({
    mode: 'history', //加入该行代码之后，网址不再带有#
    routes:[
        {
            path:'/AppMain',
            name: 'AppMain',  // 路由名称
            component:AppMain, // 组件对象
            children:[
                {
                    // path:'DrugShape', //路径带/才可实现
                    path:'/PatientMain',
                    component:PatientMain
        
                },
                {
                    path:'/ProfesMain',
                    component:ProfesMain
        
                }                
            ]
        },  
             
    ]
})