<template>
  <div>
    <div class="calendar-header">
      <button @click="prevMonth">&lt;</button>
      &nbsp;&nbsp;
      <span>{{ currentMonth.format('YYYY年M月') }}</span>
      &nbsp;&nbsp;
      <button @click="nextMonth">&gt;</button>
    </div>
    <table class="calendar-table">
      <thead>
        <tr>
          <th v-for="day in daysOfWeek" :key="day">{{ day }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(week, index) in weeks" :key="index">
          <td v-for="day in week" :key="day.date" :class="getDayClasses(day)">
            {{ day.date }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import moment from 'moment';
import _ from 'lodash';

export default {
  data() {
    return {
      currentMonth: moment(),
      daysOfWeek: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
      highlightedRanges: [
        { start: moment().startOf('day'), end: moment().endOf('day').add(2, 'days') },
        { start: moment().startOf('month').add(5, 'days'), end: moment().endOf('month').subtract(5, 'days') }
      ],
      weeks: []
    };
  },
  computed: {
    weeks() {
      const startOfMonth = this.currentMonth.clone().startOf('month');
      const endOfMonth = this.currentMonth.clone().endOf('month');
      const startDate = startOfMonth.startOf('isoWeek');
      const endDate = endOfMonth.endOf('isoWeek');
      const weeks = [];
      let currentDate = startDate.clone();

      while (currentDate.isSameOrBefore(endDate)) {
        const week = [];
        for (let i = 0; i < 7; i++) {
          week.push({
            date: currentDate.date(),
            isCurrentMonth: currentDate.isSame(this.currentMonth, 'month')
          });
          currentDate.add(1, 'day');
        }
        weeks.push(week);
      }

      return weeks;
    }
  },
  watch: {
    currentMonth() {
      this.updateWeeks();
    }
  },
  mounted() {
    this.updateWeeks();
  },
  methods: {
    prevMonth() {
      this.currentMonth.subtract(1, 'month');
    },
    nextMonth() {
      this.currentMonth.add(1, 'month');
    },
    getDayClasses(day) {
      const classes = [];

      if (!day.isCurrentMonth) {
        classes.push('other-month');
      }

      const dayMoment = this.currentMonth.clone().date(day.date);
      const isHighlighted = this.highlightedRanges.some(range =>
        dayMoment.isBetween(range.start, range.end, null, '[]')
      );

      if (isHighlighted) {
        classes.push('highlighted');
      }

      return classes.join(' ');
    },
    updateWeeks() {
      const startOfMonth = this.currentMonth.clone().startOf('month');
      const endOfMonth = this.currentMonth.clone().endOf('month');
      const startDate = startOfMonth.startOf('isoWeek');
      const endDate = endOfMonth.endOf('isoWeek');
      const weeks = [];
      let currentDate = startDate.clone();

      while (currentDate.isSameOrBefore(endDate)) {
        const week = [];
        for (let i = 0; i < 7; i++) {
          week.push({
            date: currentDate.date(),
            isCurrentMonth: currentDate.isSame(this.currentMonth, 'month')
          });
          currentDate.add(1, 'day');
        }
        weeks.push(week);
      }

      this.weeks = weeks;
    }
  }
};
</script>
  
<style>
  .calendar-header {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .calendar-table {
    width: 80%;
    border-collapse: collapse;
    margin: 5% auto;
  }
  
  .calendar-table th,
  .calendar-table td {
    padding: 10px;
    text-align: center;
    border: 1px solid #ccc;
  }
  
  .other-month {
    color: #999;
  }
  
  .highlighted {
    background-color: #ffcc00;
  }
</style>
  