<template>
    <div class="dosage_container clearfix">
        <h2 style="color: #00cccc;text-align: center; font-size: 30px;margin-top: 15px;margin-bottom: 15px;">您目前的适应症是&nbsp;&nbsp;<span style="color: deepskyblue;">{{indication}}</span>&nbsp;,您推荐的药物用法用量如下：</h2>
        <div class="dosage_content clearfix">
            <!-- <div class="drugMethod">
                <drugMethod></drugMethod>  此处需隐藏，因之前未使用组件的方式
                <h4 style="font-size: large;">用药方法 Medication method</h4>
                <br>
                <div class="drug_method_img">
                    <img src="../assets/images/oral_taken.png" alt="口服">
                </div>
                <span class="annotation">口服</span>
            </div> -->
            <div class="daily_dose">
                <h3 style="font-size: 28px;">剂量&频次 Administration & Dosage</h3> 
                <!-- <img src="../assets/images/oral_taken.png" alt="口服" width="80px" border="2px solid black">&nbsp;&nbsp; -->
                <!-- <span class="annotation" style="color:deepskyblue;">口服</span>                -->
                <div class="day">                                    
                    <div id="freq_once_day_box" class="clearfix">
                        <!-- <img src="../assets/images/" alt=""> -->
                        <!-- 确定的次数，用实线框 -->
                        <!-- v-for 中的循环次数使用 index of 来控制 -->
                        <div class="freq_once_day_certain" v-for="index of (+per_day_start)" :key="'different3'+ index" clearfix>
                            <img src="../assets/images/oral_taken.png" alt="口服">
                            <div class="div_line">8h</div>
                            <!-- <span>----------</span> -->
                        </div>

                        <!-- 不确定的次数，用虚线框 -->
                        <div class="freq_once_day_uncertain" v-for="index of (per_day_end - per_day_start)" :key="'different4'+ index" clearfix>
                            <img src="../assets/images/oral_taken.png" alt="口服">
                            <div class="div_line">8h</div>
                        <!-- <div v-for="index of 5" :key="index">                     -->
                        <!-- <div style="float:left;margin:5px;width: 50px;height: 50px;background-color: red;"></div> -->
                        </div>

                        <!-- <div id="freq_once_day"></div> -->
                    </div>
                    <div id="anno_group">                        
                        <div class="annotation"><span style="color:#00cccc;">口服</span>，每&nbsp;&nbsp;<span style="color:#00cccc;">8小时</span>&nbsp;&nbsp;一次</div>
                        <div class="annotation">24小时不超过&nbsp;&nbsp;<span style="color:#00cccc;">{{freq_once_day}}</span>&nbsp;&nbsp;次</div>
                    </div>
                    
                </div>
                <div class="slice">                                    
                    <div id="slice_box">
                        <!-- 确定的次数，用实线框 -->
                        <!-- v-for 中的循环次数使用 index of 来控制； 一个template中有两个一样的v-for  需要更改key，解决办法：将其中一个改为 :key="'different'+index" -->
                        <div class="freq_once_slice_certain" v-for="index of (+per_once_start)" :key="'different'+ index">
                            <img src="../assets/images/pill.png" alt="">
                        </div>

                        <!-- 不确定的次数，用虚线框 -->
                        <div class="freq_once_slice_uncertain" v-for="index of (per_once_end - per_once_start)" :key="'different2'+ index">
                            <img src="../assets/images/pill.png" alt="">
                        <!-- <div v-for="index of 5" :key="index">                     -->
                        <!-- <div style="float:left;margin:5px;width: 50px;height: 50px;background-color: red;"></div> -->
                        </div>
                        <!-- <div id="freq_once_slice">0.65g</div>                         -->
                    </div>
                    <div class="annotation">一次&nbsp;&nbsp;<span style="color:#00cccc;">{{freq_once_slice}}</span>&nbsp;&nbsp;片</div>
                </div>
            </div>
            <div class="med_days">
                <h3 style="font-size: 28px;">吃药天数 Days of Taking Medicine</h3>
                <br>
                <!-- <img src="../assets/images/calandar.png" width="120px"> -->
                <div class="med_days_content">
                    <!-- 方法一：根据数据来绘制多个div，可实现 -->
                    <!-- 确定的次数，用实线框 -->
                    <!-- v-for 中的循环次数使用 index of 来控制，
                        为了避免多个v-for循环之间的key因为不唯一而报错，可写成 :key="'different6'+index" 的形式，即添加different6                    
                    -->

                    <div id="med_days_box" class="clearfix">
                        <!-- <div id = "med_days_certain" class="clearfix"> -->
                        <div
                            class="med_days_certain" 
                            v-for="index of (+num_days_start)" 
                            :key="'different5'+index"
                            :class="{ highlight: dataChange1 }"
                        >
                            {{ formatDate(getDateByIndex(index-1))[1] }}
                            <!-- {{ dataChange1 }} -->
                        </div>
                        <!-- </div>                        -->
                        <!-- <div id = "med_days_uncertain" class="clearfix"> -->
                        <div
                            class="med_days_uncertain" 
                            v-for="index of (num_days_end - num_days_start)" 
                            :key="'different6'+index"
                            :class="{ highlight: dataChange2 }"    
                        >
                            <!-- {{ index + (+num_days_start)}} -->
                            {{ formatDate(getDateByIndex((index-1) + (+num_days_start)))[1] }}
                        </div>
                        <!-- </div> -->
                        
                        <!-- <div class="med_days_no_spec" v-for="index of (31 - num_days_end)" :key="'different7'+index">
                            {{ index + (+num_days_end)}} 此处不再需要
                            {{ formatDate(getDateByIndex((index-1) + (+num_days_end))) }}
                        </div> -->
                    </div>
                    <!-- 方法三：渲染日历 -->
                    <!-- <div id = "calendar">
                        <calendar_highlight />
                        <Calendar_toggleMonth />
                    </div> -->
                    <div id = med_days_annotation>
                        <div class="annotation">一共需服药&nbsp;&nbsp;<span id = "med_days" class="dynamicData">{{med_days}}</span>&nbsp;&nbsp;天</div>
                        <!-- <br> -->
                        <div style="margin:15px auto;padding: 10px;">
                            <div class="annotation" style="text-align: left;">从&nbsp;&nbsp;<span class="dynamicData">{{formatDate(date)[0]}}</span>&nbsp;&nbsp;日（今日）开始，您应服药&nbsp;&nbsp;<span id="num_days_start" class="dynamicData">{{ num_days_start }}</span>&nbsp;&nbsp;天，即到&nbsp;&nbsp;<span id = "endDate_certain" class="dynamicData">{{ formatDate(getDateByIndex(num_days_start-1))[0] }}</span>&nbsp;&nbsp;日；</div>
                            <div class="annotation" style="text-align: left;">若&nbsp;&nbsp;<span style="color:#00cccc;">到期未好转</span>，<span style="color:#00cccc;">停药</span>&nbsp;&nbsp;并咨询医师。</div>
                            <!-- <div class="annotation" style="text-align: left;">但不应服药超过&nbsp;&nbsp;<span class="dynamicData">{{num_days_end}}</span>&nbsp;&nbsp;天，即不应超过&nbsp;&nbsp;<span id = "endDate_uncertain" class="dynamicData">{{ formatDate(getDateByIndex(num_days_end-1))[0] }}</span>&nbsp;&nbsp;日</div> -->
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
    </div>
  
</template>

<script>
// import moment from 'moment';
// import _ from 'lodash';
// import 'moment/locale/zh-cn'; // 导入中文语言包
import {mapState, mapGetters} from 'vuex';
import * as d3 from "d3"; 
import calendar_toggleMonthVue from './calendar_toggleMonth.vue';
import calendar_highlight from './calendar_highlight.vue';
import Calendar_toggleMonth from './calendar_toggleMonth.vue';
// import drugMethod from './drugMethod.vue'
// import DailyDose from './DailyDose.vue';
// import MedDays from './MedDays.vue';
export default {
    name:"Dosage",
    components: { 
        calendar_highlight,
        Calendar_toggleMonth
},
    data() {
        return {
            date: new Date(), // 使用date作为数据属性来表示当前日期
            dataChange1: false, // 变化标志位，用于实现用药天数的前注意
            dataChange2: false, // 变化标志位，用于实现用药天数的前注意
        };
},
    computed:{   
        ...mapState(['indication_drug_use','indication', 'freq_once_slice', 'freq_once_day', 'med_days', 'per_once_start','per_once_end', 'per_day_start', 'per_day_end' , 'num_days_start', 'num_days_end']),
        // ...mapGetters(['showDosageInfo'])
    },
    watch:{
        // 监视元素med_days，当其值发生改变时，修改其样式；似乎只能监听一个元素的一次变化
        // todo:实现多个属性的多次监听。
        med_days:{
            handler(newValue,oldValue){
                console.log("med_days被修改了",newValue,oldValue);
                document.getElementById("med_days").classList.add("preAttentive")
                document.getElementById("endDate_certain").classList.add("preAttentive")
                document.getElementById("num_days_start").classList.add("preAttentive")
                // document.getElementsByClassName("med_days_certain").style.borderColor = "red";//此处用id可实现更改，但仅有第一个为此id的元素可实现
            }
        }, 
        num_days_start: function(newValue, oldValue) {
            // 在num_days_start发生变化时执行的逻辑
            // 检查条件并修改dataChange的值
            if (newValue !== oldValue) {
                console.log("num_days_start被修改了",newValue,oldValue);
                this.dataChange1 = true;
                document.getElementById("med_days_box").style.border = "10px dashed #fec44f";
                // document.getElementById("med_days_certain").style.border = "10px dashed rgba(255, 255, 0, 0.6)";
                // document.getElementById("med_days_certain").style.padding = "5px";
                // document.getElementById("med_days_annotation").style.border = "10px dashed yellow";
                // document.getElementById("med_days_annotation").style.padding = "5px";              
                
            }
            else {
                console.log("num_days_start未被修改",newValue,oldValue);
                document.getElementById("med_days_box").style.border = "none";
                // document.getElementById("med_days_box").style.padding = "3px";
            }
        },
        num_days_end: function(newValue, oldValue) {
            // 在num_days_end发生变化时执行的逻辑
            // 检查条件并修改dataChange的值
            if (newValue !== oldValue) {
                console.log("num_days_end被修改了",newValue,oldValue);
                this.dataChange2 = true;
                // document.getElementById("med_days_uncertain").style.border = "5px dashed yellow";
                // document.getElementById("med_days_uncertain").style.padding = "5px";
            }
        }    
    },
    mounted(){
        // this.showPath();
        // this.renderUsage();
    },
    methods: {
        formatDate(date) {
            const year = date.getFullYear()
            const month = ('0' + (date.getMonth() + 1)).slice(-2)
            const day = ('0' + date.getDate()).slice(-2)

            const dayOfWeek = date.getDay();
            // const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
            const weekdays = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
            const weekday = weekdays[dayOfWeek];
            // const month = date.getMonth() + 1
            // const day = date.getDate()
            // return `${year}-${month}-${day}`
            // return `${month}-${day}`
            //  想要返回多个值，可以将这些值组合成一个数组或对象，然后返回该数组或对象。
            const formattedDate1 = `${year}-${month}-${day}`;
            const formattedDate2 = `${month}-${day} ${weekday}`;

            return [formattedDate1, formattedDate2];

        },
        getDateByIndex(index) {
            const currentDate = new Date()
            currentDate.setDate(currentDate.getDate() + index)
            return currentDate
        },
        // showPath(){ 该部分显示路径在 usage 组件中已经实现
        //     if (indication === "fever") {
        //         // document.getElementById("fever_img").style.backgroundColor = "deepskyblue";
        //         document.getElementById("fever_path").style.visibility = "visible"; 
        //         document.getElementById("pain_path").style.visibility = "hidden";
        //         document.getElementById("dosage_container").style.borderColor = "deepskyblue"; //未生效，选不中元素  
        //     }
        //     else if (indication === "pain"){
        //         document.getElementById("pain_path").style.visibility = "visible";
        //         document.getElementById("fever_path").style.visibility = "hidden"; 
        //         document.getElementById("dosage_container").style.borderColor = "deepskyblue";  //未生效，选不中元素
        //     }

            // if (indication === "pain"){
            //     document.getElementById("pain_path").style.visibility = "visible";
            //     document.getElementById("fever_path").style.visibility = "hidden"; 
            //     // this.freq_once_slice = "2-3";
            //     // this.freq_once_day = "3-5";
            //     // this.med_days = "6-9";
            // }
        // },
        renderUsage(){
            // for(let i = 0; i < (+ this.$store.state.num_days_start); i++){
            //     document.querySelector(`#med_days_box2 .num_days:nth-child(${i+1})`).classList.add('certain_days')
            // }
            // this.$store.state.num_days_start?
            // this.num_days_start?
            // for(let j = (+ this.$store.state.num_days_start); j < (+ this.$store.state.num_days_end); j++){
            //     document.querySelector(`#med_days_box2 .num_days:nth-child(${j+1})`).classList.add('uncertain_days')
            // }

            // 无法获取元素并设置样式
            // document.getElementsByClassName('num_days')[1].style.color = 'green';

            // 单个能实现，看起来是querySelector的问题
            // this.$el.querySelector(`#med_days_box2 .num_days:nth-child(1)`).classList.add('certain_days')
        }
    },
    
}
</script>

<style>
.dosage_container{
    width: 100%;
}
.dosage_container .dosage_content{
    display: flex;
    text-align: center;
}
.dosage_container .dosage_content .annotation {
    font-size: 24px;
    font-weight: 700;
}
.dosage_container .dosage_content .daily_dose{
    float: left;
    flex-grow: 1;
    width: 300px;
    border-right: 1px solid black;
    margin-right: 5px;
    padding-right: 30px;
}
.dosage_container .dosage_content .daily_dose .slice {
    height: 200px;
}
.dynamicData{
    color: #00cccc;
}
.preAttentive{
    font-size: 32px;
    color: red;
}

#slice_box{
    display: flex;
    width: 320px;
    margin: 8px auto;
    flex-wrap:wrap;
    justify-content:space-evenly;
}
#slice_box img {
    width: 100%;
}
#slice_box .freq_once_slice_certain {
    float: left;
    width: 120px;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    line-height: 30px;
    margin: 20px 3px;
    border-radius: 110px 50px;
    border: 8px solid #00cccc;
}
#slice_box .freq_once_slice_uncertain {
    float: left;
    width: 120px;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    line-height: 30px;
    margin: 20px 3px;
    border-radius: 100px 50px;
    border: 8px dashed #00cccc;
}

.dosage_container .dosage_content .daily_dose .day {
    display: flex;
    flex-wrap:wrap;
    height: 250px;
    margin: 40px 0;
}
#anno_group {
    flex-grow: 1;
    width: 320px;
}
#freq_once_day_box{
    display: flex;
    flex-grow: 1;
    width: 320px;
    height: 100px;
    margin: 30px auto;
    flex-wrap:wrap;
    justify-content: center;
    align-content: center;
}
#freq_once_day_box .freq_once_day_certain {
    float: left;
    position: relative;
    display: flex;
    width: 180px;
    height: 100px;
    margin: 8px 0;
}
#freq_once_day_box .freq_once_day_uncertain {
    float: left;
    position: relative;
    display: flex;
    width: 180px;
    height: 100px;
    margin: 8px 0;
}
#freq_once_day_box .freq_once_day_certain img,
#freq_once_day_box .freq_once_day_uncertain img {
    flex-grow: 0;
    float: left;
    width: 100px;
    border: 2px solid black;
}
#freq_once_day_box .freq_once_day_certain .div_line,
#freq_once_day_box .freq_once_day_uncertain .div_line {
    flex-grow: 1;
    position: absolute;
    right: 0;
    top: 48px;
    float: right;
    width: 80px;
    height: 8px;
    font-size: 24px;
    background-color: #00cccc;
}
#freq_once_day{
    float: left;
    flex-grow: 0;
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    line-height: 50px;
    margin: 10px 3px;
    border: 1px solid #333;
}
.dosage_container .dosage_content .med_days{
    float: right;
    flex-grow: 1;
    width: 420px;
}
.dosage_container .dosage_content .med_days .med_days_content{
    height: 360px;
}
#med_days_box{
    display: flex;
    width: 700px;
    font-weight: 700;
    font-size: 24px;
    margin: 80px auto;
    flex-wrap:wrap;
    align-content: center;
}
#med_days_box .med_days_certain {
    float: left;
    width: 86px;
    height: 86px;
    line-height: 43px;
    text-align: center;
    border: 1px solid black;
    margin: 5px;
    background-color: #7fcdbb;
}
#med_days_box .med_days_uncertain {
    float: left;
    width: 86px;
    height: 86px;
    line-height: 43px;
    text-align: center;
    border: 1px solid black;
    margin: 5px;
    background-color: #deebf7;
}
#med_days_box .med_days_no_spec {
    float: left;
    width: 60px;
    height: 60px;
    line-height: 30px;
    text-align: center;
    border: 1px solid black;
    margin: 2px;
}
#med_days_box2 {
    display: flex;
    width: 450px;
    margin: 30px auto;
    flex-wrap:wrap;
}
#med_days_box2 .num_days {
    float: left;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border: 1px solid black;
    margin: 2px;
}
.certain_days {
    background-color: greenyellow;
}
.uncertain_days {
    background-color: goldenrod;
}
.highlight {
    border: 5px solid yellow;
}
</style>