                <div class="dropdown-content">
                    可用于普通感冒或流行性感冒引起的发热。
                </div>             
            </div>
            <div class="pain clearfix" @click="showIndication('疼痛')">
            <!-- <div class="clearfix"> -->
                <img id="pain_img" src="../assets/images/indication_pain.png" alt="pain" title="">
                <!-- <br> -->
                <div style="text-align: center;font-weight: 700;">疼痛</div>
                <!-- <p class="desc">可用于缓解轻至中度<b style="color: red;">疼痛</b>如头痛、关节痛、偏头痛、牙痛、肌肉痛、神经痛、痛经。</p>-->
