                    可用于缓解轻至中度疼痛如头痛、关节痛、偏头痛、牙痛、肌肉痛、神经痛、痛经。
                </div>  
                <!-- /* 文本悬浮方式二：Image Hover Overlay --Create an overlay effect on hover:(L70-L99 overlay) */ -->
                <!-- <div class="overlay">
                    <div class="text">可用于缓解轻至中度疼痛如头痛、关节痛、偏头痛、牙痛、肌肉痛、神经痛、痛经。</div>
                </div> -->
            </div>
          </div>  
        </div>
        <div class="path">
          <svg width="156" height="600" xmlns="http://www.w3.org/2000/svg">
            <!-- <path d="M 0 150 L 156 250" stroke="black" fill="transparent"/> -->
