            <path id="pain_path" d="M 0 370 C 50 400, 100 400, 156 250" stroke="deepskyblue" stroke-width="10" fill="transparent"/> -->
            <!-- 以下两条为直线 -->
            <path id="fever_path" d="M 0 180 L145 180 Z" stroke="deepskyblue" stroke-width="8" fill="transparent"/>
            <path id="pain_path" d="M 0 500 L145 500 Z" stroke="deepskyblue" stroke-width="8" fill="transparent"/>
