</template>

<script>
import Dosage from './Dosage.vue'
// import Indications from './Indications.vue';
// import Attention from './Attention.vue'
export default {
  name: "Usage",
  components: { 
    Dosage
},
  mounted(){
    // 用具有区间的数据来表示 不确定性 可能
    this.showIndication("发热");
  },
  methods:{
    showIndication(indication){
      console.log(indication);
      this.$store.commit('ChangeIndications',indication);
      if (indication === "发热") {
        // document.getElementById("fever_img").style.backgroundColor = "deepskyblue";
        document.getElementById("fever_path").style.visibility = "visible"; 
        document.getElementById("pain_path").style.visibility = "hidden";
        document.getElementById("dosage").style.border = "5px solid deepskyblue";   
      }
      else if (indication === "疼痛"){
        document.getElementById("pain_path").style.visibility = "visible";
        document.getElementById("fever_path").style.visibility = "hidden"; 
        document.getElementById("dosage").style.border = "5px solid deepskyblue"; 

      }

    }
  }
};

</script>

<style>
#fever_path,
#pain_path {
