  z-index: 1;
}
.indication_content .fever:hover .dropdown-content,
.indication_content .pain:hover .dropdown-content {
  display: block;
  background-color: yellow;
}

/* Image Hover Overlay --Create an overlay effect on hover:(L70-L99 overlay) */
