  height: 100%;
  width: 80%;
  opacity: 0;
  transition: .5s ease;
  background-color: rgb(0, 140, 186);
}

.indication_content .pain:hover .overlay {
  opacity: 0.6;
}

.text {
  color: yellow;
  font-size: 20px;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.indication_content p{
  float: right;
  width: 130px;
  font-size: 16px;
  padding: 5px;
}
.usage_container #dosage{
