<template>
    <div>
      <button @click="previousMonth">&lt;</button>
      &nbsp;&nbsp;
      <span>{{ currentMonth }}</span>
      &nbsp;&nbsp;
      <button @click="nextMonth">&gt;</button>
  
      <table class="calendar-table">
        <thead>
          <tr>
            <th v-for="day in weekDays" :key="day">{{ day }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="week in calendar" :key="week">
            <td v-for="date in week" :key="date">{{ date }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        currentDate: new Date(), // 使用currentDate作为数据属性来表示当前日期
        weekDays: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'] // 定义了当前日期和星期几的数组
      };
    },
    computed: {
      currentMonth() {
        // 使用计算属性currentMonth获取当前年份和月份，并在模板中显示出来。
        const year = this.currentDate.getFullYear();
        const month = this.currentDate.getMonth() + 1;
        return `${year}年${month}月`;
      },
      calendar() {
        // 在calendar计算属性中，我们通过获取当前月份的第一天是星期几和
        // 最后一天的日期来生成一个二维数组calendar，其中每个元素表示一个
        // 星期的日期。
        // 我们在模板中使用两个v-for指令来渲染日历的表头和日期。
        const year = this.currentDate.getFullYear();
        const month = this.currentDate.getMonth();
        const firstDay = new Date(year, month, 1).getDay() -1; // 获取当前月份第一天是星期几
        const lastDate = new Date(year, month + 1, 0).getDate(); // 获取当前月份的最后一天日期
  
        let calendar = [];
        let week = [];
        for (let i = 0; i < firstDay; i++) {
          week.push('');
        }
        for (let i = 1; i <= lastDate; i++) {
          week.push(i);
          if (week.length === 7) {
            calendar.push(week);
            week = [];
          }
        }
        if (week.length > 0) {
          calendar.push(week);
        }
  
        return calendar;
      }
    },
    methods: {
      // 当点击切换按钮时，我们调用previousMonth或nextMonth方法
      // 修改currentDate的值，然后视图会自动重新渲染。
        previousMonth() {
            const newDate = new Date(this.currentDate);
            newDate.setMonth(newDate.getMonth() - 1);
            this.currentDate = newDate;
            },

        nextMonth() {
            const newDate = new Date(this.currentDate);
            newDate.setMonth(newDate.getMonth() + 1);
            this.currentDate = newDate;
        }
    }
  };
</script>

<style>
.calendar-table {
  width: 80%;
  border-collapse: collapse;
  margin: 20px auto;
}

.calendar-table th,
.calendar-table td {
  border: 1px solid #000;
  padding: 8px;
}
/* 当前日期 */
.current-day {
  background-color: yellow;
  font-weight: bold;
}
/* 确定的用药时间范围 */
.highlight1 {
  background-color: blue;
  color: white;
}
/* 不确定的用药时间范围 */
.highlight2 {
  background-color: green;
  color: white;
}

</style>
  