        <br>
        <h3 class="spec_title">
            药物过量 Overdose
        </h3>
        <br>
        <div class="overdose_adverse">
        <ol>
            <li>对乙酰氨基酚在<b>成年人和青少年（≥12 岁）</b>中，在 <b>8 小时或以内</b>的时间内摄入<b>超过7.5-10g </b>后发生<b>肝毒性</b>。</li>
