                                ]
                            }                            
                        ]
                    },
                    {
                        name: '互相之间的影响',
                        level: 1, // 添加表示层级的属性
                        group: 3,
                        children: [
                                {
                                    name: '与其他药物同时使用',
                                    level: 2, // 添加表示层级的属性
                                    group: 3.1,
                                    children: [
                                        { name: '发生药物相互作用', value: 3, level: 3 },
                                        // { name: '子层级2', value: 1 }
                                    ]
                                }
                            ]
                    },
                ]
            }
        };
    },
    mounted(){
        this.drawTree();
        this.drawIciclePlot();
        this.generateComponentMap();
        this.generateInterMap();
        this.drawGroup();
    },
    methods:{
        generateComponentMap(){
            var svg = d3.select("#component")
                .append("g")
                .attr("transform", "translate(130,125)");
            // 绘制内层的圆
            var circle = svg.append("circle")
            circle.attr("cx",0)
                .attr("cy",0)
                .attr("r",60)
                .style("cursor", "pointer") // 设置光标为小手图标
                .attr("fill","antiquewhite")
            //Adding text labels to the circle
            var text1 = svg.append("text");
            //Styling text
            text1.attr("font-family",  "Courier")
                .attr("fill", "black")
                .attr("dx",2)
                .attr("dy",2)
                .text("对乙酰氨基酚")
                // .style("opacity", "0.8")
                .attr("font-size", "1em")
                .attr("font-weight", "700")
                .attr("text-anchor",  "middle");
            
            // 鼠标悬浮内层圆的处理函数
            circle.on("mouseover", function() {
                d3.select(this)
                    .attr("fill", "rgb(250,250,120)")  // 修改内层圆的填充颜色
                    .attr("stroke", "black")  // 添加内层圆的描边颜色
                    .attr("stroke-width", 2);  // 设置内层圆的描边宽度
            });

            // 鼠标离开内层圆的处理函数
            circle.on("mouseout", function() {
                d3.select(this)
                    .attr("fill", "antiquewhite")  // 恢复内层圆的原始填充颜色
                    .attr("stroke", "none");  // 移除内层圆的描边
            });
  
            // Function is used 绘制外层的弧形
            var arc = d3.arc()
                .innerRadius(60)
                .outerRadius(120)
                .startAngle(100)
                .endAngle(2 * 180);
    
            svg.append("path")
                .attr("class", "arc")
                .attr("d", arc)
                .attr("fill"," skyblue");
             //Adding text labels to the circle
             var text2 = svg.append("text");
            //Styling text
            text2.attr("font-family",  "Courier")
                .attr("fill", "black")
                .attr("dx",0)
                .attr("dy",90)
                // .text("粉状纤维素、淀粉、羟乙基纤维素、聚维酮、微晶纤维素、预胶化淀粉、羧甲基淀粉钠、硬脂酸镁、欧巴代和棕榈蜡。")
                .text("-------")
                .attr("font-size", "2em")
                .attr("font-weight", "700")
            
        },
        generateInterMap(){
            let drag = simulation => {
            function dragstarted(event) {
                if (!event.active) simulation.alphaTarget(0.3).restart()
                event.subject.fx = event.subject.x
                event.subject.fy = event.subject.y
            }
  
            function dragged(event) {
                event.subject.fx = event.x
                event.subject.fy = event.y
            }
  
            function dragended(event) {
                if (!event.active) simulation.alphaTarget(0)
                event.subject.fx = null;
                event.subject.fy = null;
            }
  
            return d3.drag()
                .on("start", dragstarted)
                .on("drag", dragged)
                .on("end", dragended)
            }

            const scale = d3.scaleOrdinal(d3.schemeCategory10)
            const color = d => scale(d.group)

            let height = 126
            let width = 160

            let data = {
            "nodes": [
                {"id": "对乙酰氨基酚", "group": 1, "size": 12},
                {"id": "肝损害", "group": 2, "size": 8},
                {"id": "增强氯霉素毒性", "group": 2, "size": 8},
                {"id": "发生药物相互作用", "group": 2, "size": 8},
                {"id": "巴比妥类（如苯巴比妥）", "group": 3, "size": 5},
                {"id": "解痉药（如颠茄）", "group": 3, "size": 5},               
                {"id": "氯霉素", "group": 3, "size": 5},                
                {"id": "其他药物", "group": 3, "size": 5}

                // {"id": "对乙酰氨基酚", "group": 1, "size": 12},
                // {"id": "巴比妥类（如苯巴比妥）", "group": 2, "size": 8},
                // {"id": "解痉药（如颠茄）", "group": 2, "size": 8},               
                // {"id": "氯霉素", "group": 2, "size": 8},                
                // {"id": "其他药物", "group": 2, "size": 8},
                // {"id": "肝损害", "group": 3, "size": 5},
                // {"id": "增强氯霉素毒性", "group": 3, "size": 5},
                // {"id": "发生药物相互作用", "group": 3, "size": 5},
                
            ],
            "links": [
                {"source": "对乙酰氨基酚", "target": "肝损害", "value": 1},
                {"source": "对乙酰氨基酚", "target": "增强氯霉素毒性", "value": 1},
                {"source": "对乙酰氨基酚", "target": "发生药物相互作用", "value": 1},
                {"source": "肝损害", "target": "巴比妥类（如苯巴比妥）", "value": 1},
                {"source": "肝损害", "target": "解痉药（如颠茄）", "value": 1},
                {"source": "增强氯霉素毒性", "target": "氯霉素", "value": 1},
                {"source": "发生药物相互作用", "target": "其他药物", "value": 1}

                // {"source": "对乙酰氨基酚", "target": "巴比妥类（如苯巴比妥）", "value": 1},
                // {"source": "对乙酰氨基酚", "target": "解痉药（如颠茄）", "value": 1},
                // {"source": "对乙酰氨基酚", "target": "氯霉素", "value": 1},
                // {"source": "对乙酰氨基酚", "target": "其他药物", "value": 1},
                // {"source": "巴比妥类（如苯巴比妥）", "target": "肝损害", "value": 1},
                // {"source": "解痉药（如颠茄）", "target": "肝损害", "value": 1},
                // {"source": "氯霉素", "target": "增强氯霉素毒性", "value": 1},
                // {"source": "其他药物", "target": "发生药物相互作用", "value": 1}
                ]
            }

            const links = data.links.map(d => Object.create(d))
            const nodes = data.nodes.map(d => Object.create(d))

            const simulation = d3.forceSimulation(nodes)
            .force("link", d3.forceLink(links).id(d => d.id))
            .force("charge", d3.forceManyBody())
            .force("center", d3.forceCenter(width / 2, height / 2))

            const svg = d3.select("#interact")
            .attr("viewBox", [10, -10, width * 1.2, height * 1.2])

            const link = svg.append("g")
                .attr("stroke", "#999")
                .attr("stroke-opacity", 0.6)
                .selectAll("line")
                .data(links)
                .join("line")
                .attr("stroke-width", d => Math.sqrt(d.value))

            const node = svg.append("g")
                .attr("stroke", "#fff")
                .attr("stroke-width", 1.5)
                .selectAll("circle")
                .data(nodes)
                .join("circle")
                .attr("r", d => d.size)
                .attr("fill", color)
                .call(drag(simulation))
            
             //添加描述节点的文字
            const text = svg.append("g")
                    .selectAll("text")
                    .data(nodes)
                    .join("text")
                    .attr("fill","black")
                    .attr("dx",d => d.size)
                    .attr("dy",d => d.size)
                    .text(d => d.id)
                    .attr("font-size",d => d.size)

            node.append("title")
                .text(d => d.id)

            simulation.on("tick", () => {
            link
                .attr("x1", d => d.source.x)
                .attr("y1", d => d.source.y)
                .attr("x2", d => d.target.x)
                .attr("y2", d => d.target.y)

            node
                .attr("cx", d => d.x)
                .attr("cy", d => d.y)
            
            text
                .attr("x", d => d.x)
                .attr("y", d => d.y)
            })   
        },
        drawIciclePlot() {
            const width = 1750;
            const height = 450;

            const svg = d3
                .select('#icicle-plot-interaction')
                .append('svg')
                .attr('width', width)
                .attr('height', height);

            const color = d3.scaleOrdinal()
                            .range(['#2b8cbe', '#4eb3d3', '#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
                            // .range(["#4eb3d3","#7bccc4","#a8ddb5","#ccebc5","#e0f3db","#f7fcf0"])

            const partition = d3.partition().size([width, height]);
            
            


            const root = d3.hierarchy(this.IcicleData)
                .sum((d) => d.value)
                .sort((a, b) => b.value - a.value);

            partition(root);

            const cell = svg
                .selectAll('g')
                .data(root.descendants())
                .join('g')
                .attr('transform', (d) => `translate(${d.x0},${d.y0})`)
                .attr('class', 'rectangle') //添加rectangle类

            cell
                .append('rect')
                .attr('width', (d) => d.x1 - d.x0)
                .attr('height', (d) => d.y1 - d.y0)
                // 根据数据节点的 group 属性来设置 SVG 元素的 fill 属性，颜色的选择取决于节点是父节点还是子节点。
                .attr('fill', (d) => color((d.children ? d : d.parent).data.group))
                // .attr('fill', (d) => color(d.data.level))
                

            
                // 在foreignObject元素内部创建了一个div元素，并使用CSS样式将其设置为占据foreignObject的整个宽度和高度。
                // 通过设置display: flex、justify-content: center和align-items: center，将文本内容水平和垂直居中显示在单元格中。
                cell
                .append('foreignObject')
                .attr('x', 4) // 偏移量，根据需求调整
                .attr('y', 4) // 偏移量，根据需求调整
                .attr('width', (d) => d.x1 - d.x0 - 8) // 减去边距的宽度
                .attr('height', (d) => d.y1 - d.y0 - 8) // 减去边距的高度
                .append('xhtml:div')
                // .style('width', (d) => d.x1 - d.x0 - 8 + 'px') // 减去边距的宽度
                // .style('height', (d) => d.y1 - d.y0 - 8 + 'px') // 减去边距的高度
                // .style('overflow', 'auto') // 添加滚动条，根据需要调整
                .style('width', '100%')
                .style('height', '100%')
                .style('display', 'flex')
                .style('justify-content', 'center')
                .style('align-items', 'center')
                // .style('color', 'white')
                .style('font-weight',700)
                // .style('title',(d) => d.data.name)
                .html((d) => d.data.name)
                .on('mouseover', function () {
                    d3.select(this)
                        .style('background-color', 'rgba(255, 255, 120)'); // 在鼠标悬浮时改变背景颜色为黄色
                })
                .on('mouseout', function () {
                    d3.select(this)
                        .style('background-color', null); // 在鼠标移出时恢复原始背景颜色
                });

                // ...

        },
        drawTree() {
            const data = {
                name: '对乙酰氨基酚',
                children: [
                    {
                        name: '对本药的影响',
                        children: [
                            { 
                                name: '正向',                                
                            },
                            { 
                                name: '负向',
                                children: [
                                    { 
                                        name: '长期应用本品可致肝损害',
                                        children: [
                                            { name: '应用巴比妥类（如苯巴比妥）者' },
                                            { name: '应用解痉药（如颠茄）者' },
                                        ], 
                                    },
                                    // { 
                                    //     name: '应用巴比妥类（如苯巴比妥）者',
                                    //     children: [
                                    //         { name: '长期应用本品可致肝损害' },
                                           
                                    //     ], 
                                    // },
                                    // { 
                                    //     name: '应用解痉药（如颠茄）者',
                                    //     children: [
                                    //         { name: '长期应用本品可致肝损害' },
                                            
                                    //     ], 
                                    // },
                                ], 
                            
                            },
                        ],
                    },
                    {
                        name: '对其他药物的影响',
                        children: [
                            { 
                                name: '正向',
                            },
                            { 
                                name: '负向',
                                children: [                                    
                                    { 
                                        name: '增强氯霉素的毒性',
                                        children: [
                                            { name: '与氯霉素同服' },                                          
                                        ], 
                                    },                                    
                                ], 
                            
                            },
                        ],
                    },
                    {
                        name: '互相之间的影响',
                        children: [
                                    { 
                                        name: '发生药物相互作用',
                                        children: [
                                            { name: '与其他药物同时使用' },                                          
                                        ], 
                                    },  
                                ], 
                        // children: [
                        //     { 
                        //         name: '正向',
                                
                        //     },
                        //     { 
                        //         name: '负向',
                        //         children: [
                        //             { 
                        //                 name: '发生药物相互作用',
                        //                 children: [
                        //                     { name: '与其他药物同时使用' },                                          
                        //                 ], 
                        //             },  
                        //         ], 
                            
                        //     },
                        // ],
                    },
                ],
            };

            const width = 1780;
            const height = 1000;
            const scale = d3.scaleOrdinal(d3.schemeCategory10)
            // const color = d => scale(d.group)
            
            const svg = d3.select(this.$refs.treeContainer)
                        .attr("width", width)
                        .attr("height", height)
                        .append("g")
                        .attr("transform", "translate(100,0)");
            
            // const root = hierarchy(data);
            // const treeLayout = tree().size([1000, 1200]); // 调整布局大小

            const root = d3.hierarchy(data);
            // const root = d3.hierarchy(this.IcicleData);
            const treeLayout = d3.tree().size([1000, 1200]); // 调整布局大小

            // 设置树图的尺寸，高度根据层级深度自适应
            // const treeLayout = d3.tree().size([height, width]);

            treeLayout(root);

            // 定义箭头标记
            svg.append("defs")
                .append("marker")
                .attr("id", "arrowhead")
                .attr("viewBox", "0 -5 10 10")
                .attr("refX", 18)  // Adjust this value to control the position of the arrowhead along the link
                .attr("refY", 0)
                .attr("markerWidth", 16)
                .attr("markerHeight", 16)
                .attr("orient", "auto")
                .append("path")
                .attr("d", "M0,-5L10,0L0,5") // 箭头路径
                .attr("fill", "rgb(255, 0, 0, 0.6)"); // 修改箭头的颜色
            
            const links = svg.selectAll('.link')
                .data(root.links())
                .join('path')
                .attr('class', 'link')
                .attr('d', (d) => {
                    // return `M${d.source.y},${d.source.x} ${d.target.y},${d.target.x}`;
                    // return `M${d.source.y},${d.source.x}C${(d.source.y + d.target.y) / 2},${d.source.x} ${(d.source.y + d.target.y) / 2},${d.target.x} ${d.target.y},${d.target.x}`;
                    return `M${d.target.y},${d.target.x}C${(d.source.y + d.target.y) / 2},${d.target.x} ${(d.source.y + d.target.y) / 2},${d.source.x} ${d.source.y},${d.source.x}`;

                });

            links.filter((d) => d.target.data.name === '应用巴比妥类（如苯巴比妥）者' || d.target.data.name === '应用解痉药（如颠茄）者' 
                                || d.target.data.name === '与氯霉素同服' || d.target.data.name === '与其他药物同时使用')
                .attr('marker-end', 'url(#arrowhead)'); 

            const nodeSize = 40; // 定义节点的尺寸

            const nodes = svg
                .selectAll(".node")
                .data(root.descendants())
                .join("g")
                .attr("class", "node")
                .attr("transform", (d) => `translate(${d.y},${d.x})`);

            nodes.append("circle").attr("r", 20);

            // 定义“对本药的影响”节点的图元形状 —— 矩形（rectangle）
            const rectWidth = 50;
            const rectHeight = 50;

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "对本药的影响") // 过滤出对应的节点
                .append("rect")
                .attr("x", -rectWidth / 2)
                .attr("y", -rectHeight / 2)
                .attr("width", rectWidth)
                .attr("height", rectHeight)
                .attr("rx", 5) // 可以添加圆角效果
                .attr("ry", 5)
                .style("fill","#87CEFA;")
                // .style("fill", "lightblue");

            // 定义“对其他药物的影响”节点的图元形状 —— 菱形（diamond）
            const diamondSize = 30;

            const diamondPoints = [
                    [0, -diamondSize],
                    [diamondSize, 0],
                    [0, diamondSize],
                    [-diamondSize, 0],
                ];

            const lineFunction = d3.line().x((d) => d[0]).y((d) => d[1]).curve(d3.curveLinearClosed);

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "对其他药物的影响") // 过滤出对应的节点
                .append("path")
                .attr("d", lineFunction(diamondPoints))
                .style("fill","#87CEFA;")
                // .style("fill", "lightgreen");

            // 定义“互相之间的影响”节点的图元形状 —— 三角形（triangle）
            const triangleSize = 30;
            const triangleHeight = (Math.sqrt(3) / 2) * triangleSize;

            const trianglePoints = [
                    [0, -triangleSize],
                    [triangleHeight, triangleSize / 2],
                    [-triangleHeight, triangleSize / 2],
                ];

            nodes
                .filter((d) => d.depth === 1 && d.data.name === "互相之间的影响") // 过滤出对应的节点
                .append("path")
                .attr("d", lineFunction(trianglePoints))
                .style("fill","#87CEFA;")
                // .style("fill", "lightcoral");
            
            // 移除原来的圆形图元
            nodes
                .filter((d) => d.depth === 1 && (d.data.name === "对本药的影响" || d.data.name === "对其他药物的影响" || d.data.name === "互相之间的影响"))
                .select("circle")
                .remove();
            
            // 使用 foreignObject 元素来插入 HTML 内容，而不是直接使用 SVG 的 text 元素
            // 可实现只改变文字内容前面的箭头的颜色
            nodes.append('foreignObject')
                .attr('x', -60)
                .attr('y', -60)
                .attr('width', 400) // 根据需要设置宽度
                .attr('height', 60) // 根据需要设置高度
                .html((d) => {
                    if (d.data.name.includes('增强') || d.data.name.includes('提高')) {
                        return `<div class="tree_item">
                                    <span class="up_arrow"></span>
                                    <span class="tree_text">${d.data.name}</span>
                                </div>`;
                    } else if (d.data.name.includes('减弱') || d.data.name.includes('降低')) {
                        return `<div class="tree_item">
                                    <span class="down_arrow"></span>
                                    <span class="tree_text">${d.data.name}</span>
                                </div>`;
                    } else {
                        return d.data.name;
                        }
                })
                .on('mouseover', function() {
                    d3.select(this)
                        .style('font-size', '28px'); // 设置放大后的字体大小
                    d3.select(this.parentNode)
                        .select('circle')
                        .attr('transform', 'scale(1.5)'); // 设置节点放大效果
                })
                .on('mouseout', function() {
                    d3.select(this)
                        .style('font-size', null); // 恢复初始的字体大小
                    d3.select(this.parentNode)
                        .select('circle')
                        .attr('transform', 'scale(1)'); // 恢复节点原始大小
                });


            // 使用text的方式，文字内容和箭头的颜色均会发生改变
            // nodes.append('text')
            //     .attr('x', -60)
            //     .attr('y', -30)
            //     .style("fill","black")
            //     // .style("fill", (d) => {
            //     //     if (d.data.name.includes('增强'|| '提高')) {
            //     //         return "red"; // 修改文本前箭头的颜色
            //     //     } else {
            //     //         return "black";
            //     //     }
            //     // })
        
                // .text((d) => {
                //     if (d.data.name.includes('增强'|| '提高')) {
                //         return '\u2191 ' + d.data.name; // 在节点文本前添加箭头符号
                //     } else {
                //         return d.data.name;
                //     }
                // });
                // .text((d) => d.data.name);
            
            nodes.attr('class', (d) => {
                // 根据节点名称设置不同的 CSS 类
                if (d.data.name.includes('正向')) {
                    return 'node-positive'; // 正向节点设置 CSS 类 node-positive
                } else if (d.data.name.includes('负向')) {
                    return 'node-negative'; // 负向节点设置 CSS 类 node-negative
                } else {
                    return 'node-default'; // 其他节点设置 CSS 类 node-default
                }
            });
            
            // nodes
            //     .on('mouseover', function() {
            //         d3.select(this).select('circle')
            //             .attr('transform', 'scale(1.5)'); // 设置节点放大效果
            //         d3.select(this).select('text')
            //             .style('font-size', '30px'); // 设置文本变大，仅适用于text的方式，foreignObject并不适用
            //     })
            //     .on('mouseout', function() {
            //         d3.select(this).select('circle')
            //             .attr('transform', 'scale(1)'); // 恢复节点原始大小
            //         d3.select(this).select('text')
            //             .style('font-size', null); // 恢复文本原始字号
            //     });

        },
        drawGroup(){
            const width = 800;
            const height = 500;
            
            const svg = d3.select(this.$refs.svgContainer)
                .attr("width", width)
                .attr("height", height)
                .append("g")
                .attr("transform", "translate(-10,0)");
            
            // 定义布局比例尺
            const pack = d3.pack()
                .size([width, height])
                .padding(3);

            // 定义颜色比例尺
            // const colorScale = d3.scaleOrdinal(d3.schemeCategory10);
            const colorScale = d3.scaleOrdinal()
                        .range(['#7bccc4', '#a8ddb5', '#ccebc5', '#e0f3db', '#f7fcf0'])
            
            // 将数据转换为层级结构
            const root = d3.hierarchy(this.chartData)
                .sum(d => d.value || 0);
            
            // 使用布局算法计算圆形的位置和大小
            const nodes = pack(root).descendants();        
        
            // 绘制圆形元素
            const circles = svg.selectAll("circle")
                .data(nodes)
                .enter()
                .append("circle")
                .attr("cx", d => d.x)
                .attr("cy", d => d.y)
                .attr("r", d => d.r)
                .style("cursor", "pointer") // 设置光标为小手图标
                .attr("fill", d => colorScale(d.depth))
                
            const baseFontSize = 30;
            // 绘制文本元素
            const labels = svg.selectAll("foreignObject")  // 当添加foreignObject之后，原先对圆形添加的hover功能无法再实现
                .data(nodes)
                .enter()
                .append("foreignObject")
                .attr("x", d => d.x - d.r)
                .attr("y", d => d.y - d.r)
                .attr("width", d => d.r * 2) // 设置宽度为圆形的直径
                .attr("height", d => d.r * 2) // 设置高度为圆形的直径
                .append("xhtml:div")
                // foreignObject正常为矩形，通过添加类，在css中设置其为圆形
                .attr("class", "circle-text") // 添加自定义CSS类名
                // .style("width", d => (d.r * 2) + "px")
                // .style("height", d => (d.r * 2) + "px")
                .style("text-align", "center")
                .style("font-weight", 700)
                .style("font-size", d => {
                    // 根据深度设置文本大小
                    return baseFontSize / (d.depth * 0.5 + 1) + "px";
                })
                .style("cursor", "pointer")
                .style("word-wrap", "break-word") // 允许文本换行
                .text(d => d.data.name)
                .on("mouseover", function () {
                    d3.select(this)
                        .style("border","3px solid orange")
                        .style("font-size",  d => {
                            // 根据深度设置文本大小
                            // const baseFontSize = 32;
                            return (baseFontSize / (d.depth * 0.5 + 1))*1.2 + "px";
                    })
                })
                .on("mouseout", function () {
                    d3.select(this)
                        .style("border",null)
                        .style("font-size", d => {
                            // 根据深度设置文本大小
                            // const baseFontSize = 32;
                            return baseFontSize / (d.depth * 0.5 + 1) + "px";
                    })
                })
                .each(function (d) {
                    // 根据深度设置文本位置
                    const div = d3.select(this);
                    const circleRadius = d.r;
                    if (d.depth === 0 || d.depth === 1) {
                        const x = d.x;
                        const y = d.y;
                        div.style("padding-top", "16px");                     
                    }
                    else if (d.depth === 2) {
                        const x = d.x;
                        const y = d.y;
                        div.style("line-height", "1.2"); // 设置行高
                        div.style("align-items","center") // 使得文本垂直居中
                        // div.style("margin-top", "50%"); // 垂直居中文本
                        // div.style("transform", "translateY(-50%)"); // 垂直居中文本
                    }
            });
        }
    }
};

</script>

